import React , {useState} from 'react';
import apis from '../../../api';
import './style.scss';
import {Irespone} from '../../../constants/interface';
import { SUCCESS } from '../../../constants';
import FormatMoney from '../../../components/format-money';
import { useHistory } from 'react-router';
const OrderForm = () => {
    const idUser = localStorage.getItem('id');
    const history = useHistory();
    const [order,setOrder] = useState([])
    React.useEffect(()=> {
        apis.getOrder(idUser).then(({data}: {data: Irespone})=>{
         if(data.status === SUCCESS){
            setOrder(data.data.reverse())
         }
        })
    },[])
    return (
        <div className="container">
            { order.length > 0 ?
            <div className="row order-form">
            <h3>Theo di n hng</h3>
               <table style={{width: '100%'}}>
                   <thead>
                       <tr>
                        <th>M n hng</th>
                       <th>Ngi nhn</th>
                       <th>S in thoi</th>
                       <th>Tng s tin</th>
                       <th>Trng thi</th>
                       </tr>
                   </thead>
                   <tbody>
                       {Array.isArray(order) && order.map((item: any, index: number)=> (
                           <tr key={index} onClick={()=> history.push(`/follow-order-detail?${item._id}`,{item})}>
                               <td>OD-{item._id}</td>
                               <td>
                                    {item.infor.name}</td>
                               <td>{item.infor.phone}</td>
                               <td><FormatMoney money={item?.product?.sum_price}/></td>
                               <td><span className={item.status === 0 ? "status-default" : item.status === 1 ? "status-ship" : "status-complete"}>{item.status === 0 ? 'Ch ly hng' : item.status === 1 ? 'ang giao' : ' giao hng'}</span></td>
                           </tr>
                       ))}
                   </tbody>
               </table>
            </div>
            : 'Khng c n hng no  c t'}
        </div>
    )
}

export default OrderForm;
