import React, { useState } from "react";
import { useHistory, useParams } from "react-router";
import apis from "../../../api";
import { Image, Button, Collapse } from "antd";
import { PhoneOutlined } from "@ant-design/icons";
import "./style.scss";
import 'font-awesome/css/font-awesome.min.css';
import { SUCCESS } from "../../../constants";
import ReactImageZoom from 'react-image-zoom';
import LoadingPage from "../../../components/LoadingPage";
const { Panel } = Collapse;
const ProductDetail = () => {
  const params: any = useParams();
  const [product, setProduct]: any = useState({});
  const [color, setColor]: any = useState("");
  const [size, setSize]: any = useState("");
  const [number,setNumber] = useState(1);
  const [loading,setLoading] =useState(true);
  const history = useHistory();
  const image = { height: 800, zoomWidth: 1000, img: `${product?.image}`};
  React.useEffect(() => {
    apis.getProductDetail(params.id).then((resp) => {
      if(resp){
        setProduct(resp.data.data);
        setLoading(false);
      }
      });
  }, [params]);

  const handleColor = (e: string) => {
    setColor([{ key: e }]);
  };

  const handleSize = (e: string) => {
    setSize([{ key: e }]);
  };

  const renderDescription = (value: string) => {
      console.log(value?.[0])
      if(value?.includes('\n')){
          const data = value?.replace('\n', '\n * ') 
        return data
      }
      return value?.replace('\n', '\n * ')
    //  return data;
  }

  const handleSubtraction = () => {
    if(number > 1){
      setNumber(number - 1)
    }
    
  }
 
  const handleAddition = () => {
    setNumber(number + 1)
  }

  const userId = localStorage.getItem('id');
  const handleSubmit = () => {
     apis.CreateStore({id: userId, store: {
       _id: params.id ,
       nameproduct: product.title,
       price_sale: product.price_sale,
       color: color[0].key,
       size: size[0].key , 
       image: product.image,
       total: number
      }})
      .then((resp: any) => {
        if(resp.data.status === SUCCESS){
          history.push('/store')
        }
      })
  }

  const note_data = [
      {icon: <i className="far fa-check-circle"></i>, title: 'Cht lng sn phm?', text: 'Sn phm lun c MENSHOP kim nh, nh gi vi cht lng cao nht trc khi n tay khch hng!'},
      {icon: <i className="fas fa-adjust"></i>, title: 'Sai mu sn phm?', text: 'V 1 s yu t khch quan nh  sng mn hnh, cht lng mn hnh nn sn phm c th ko ng mu.'},
      {icon: <i className="fas fa-medal"></i>,title: 'Hng c sn khng?' ,text: 'Sn phm hin c sn ti h thng ca hng XSHOP v online ti website.'},
      {icon: <i className="fas fa-thumbs-up"></i>,title: 'Bo hnh sn phm' ,text: 'Sn phm c bo hnh trong 30 ngy vi bt k li no. Hng SALE khng c bo hnh.'},
      {icon: <i className="far fa-clock"></i>,title: 'Thi gian giao hng?',text: 'Chng ti s dng n vi vn chuyn uy tn v nhanh chng nht, thi d kin t 1-4 ngy tuy khu vc.'},
      {icon: <i className="fas fa-clock"></i>,title: 'Thi gian lm vic?',text: 'H thng ca hng v Online lm vic t 8:30 - 22:00 hng ngy.'},
      {icon: <i className="fas fa-history"></i>,title: 'i hng nh th no?',text: 'Vic i hng rt d dng v chng ti lun mun khch hng ng  nht. Hy lin h fanpage  i!'},
      {icon: <i className="fas fa-expand-alt"></i>,title: 'Chn sai size giy?',text: 'Bn c th qua ca hng hoc gi li  i hng vi sn phm mi 100%. Cn nguyn tem mc, ho n mua hng.'},
  ]

  if(loading){
    return <LoadingPage />
  }
  return (
      <>
    <div className="page-detail d-flex">
      <div className="slide-image col-6">
        {/* <Image
          style={{ objectFit: "cover" }}
          width="90%"
          height={800}
          src={`${product?.image}`}
        /> */}
        <ReactImageZoom  {...image}/>
      </div>
      <div className="col-6 description-product">
        <div className="description-product_layout">
          <h2 className="description-product_title">{product.title}</h2>
          <p className="description-product_intro">
            Cht liu cao cp, bn p theo thi gian. Thit k thi trang. Kiu
            dng phong cch  bn cao. D phi ..
          </p>
          <p>
            <span className="description-product_price">{product.price}</span>{" "}
            <span className="description-product_price-sale">
              {product.price_sale}
            </span>
          </p>
          <h5 className="description-product_label">Mu sc</h5>
          <div className="description-product_content">
            {Array.isArray(product.color) &&
              product.color.map((el: any, index: number) => (
                <Button
                  className={color[0]?.key === el ? "btn-color" : "btn-default"}
                  onClick={() => handleColor(el)}
                  key={index}
                >
                  {el}
                </Button>
              ))}
          </div>
          <h5 className="description-product_label">Kch c</h5>
          <div className="description-product_content">
            {Array.isArray(product.size) &&
              product.size.map((el: any, index: number) => (
                <Button
                  className={size[0]?.key === el ? "btn-color" : "btn-default"}
                  onClick={() => handleSize(el)}
                  key={index}
                >
                  {el}
                </Button>
              ))}
          </div>
          <h5 className="description-product_label">S lng</h5>
          <><Button onClick={handleSubtraction}>-</Button> <span className="px-2">{number}</span> <Button onClick={handleAddition}>+</Button></>
          <div className="w-100">
            <Button onClick={handleSubmit} disabled={!color || !size} className="btn-submit">
              <p>Mua ngay</p>
              <span>Giao tn nh - i tr d dng</span>
            </Button>
            <Collapse
              className="mt-4"
              accordion
              expandIconPosition="right"
              expandIcon={(props: any) => {
                console.log(props);
                if (props.isActive) {
                  return (
                    <a
                      style={{ color: "black", fontSize: "20px" }}
                      onClick={(e) => {}}
                    >
                      -
                    </a>
                  );
                } else {
                  return (
                    <a
                      style={{ color: "black", fontSize: "16px" }}
                      onClick={(e) => {}}
                    >
                      +
                    </a>
                  );
                }
              }}
            >
              <Panel header="Chnh sch giao hng & i tr" key="1">
                <div className="exchange">
                    <p><i className="fas fa-truck "></i> Giao hng hon ton min ph 100%</p>
                    <p><i className="fas fa-shield-alt"></i> An ton vi nhn hng v tr tin ti nh</p>
                    <p><i className="fas fa-exchange-alt"></i> Bo hnh i tr trong vng 60 ngy</p>

                </div>
              </Panel>
              <Panel header="Hng dn bo qun" key="2">
                <div className="preservation">
                    <p className="preservation-label">* Kh mi bn trong giy</p>
                    <div>
                        Bn hy t ti ng vin chng m vo bn trong giy  ht m v rc phn rm(c th thay bng cch t vo bn trong giy gi tr ti lc cha qua s dng)  kh mi, gip giy lun kh thong. <br />
                         hn ch mi hi v s m t cho giy, hy chn v chn loi tt, c kh nng thm ht cao. Ngoi ra, dng cc loi lt giy kh mi cng l mt phng php tt.
                    </div>
                    <p className="preservation-label mt-3">* Bo qun khi khng s dng</p>
                    <div>
                        Khi s dng giy, bn ng vi vt hp i m hy ct li  dnh. Khi khng s dng, nhet mt t giy vn vo bn trong giy  gi cho dng giy lun chun, p . Sau  t giy vo hp bo qun cng ti ht m.
                    </div>
                </div>
              </Panel>
            </Collapse>
            <h5 className="contact-switchboard mt-3">
              Tng i bn hng{" "}
              <span>
              <i className="fas fa-phone-square-alt"></i> 035.397.xxxx
              </span>
            </h5>
            <p className="contact-text">H tr 24/7 mi ngy</p>
          </div>
        </div>
      </div>
    </div>
    <div className="container-fluid">
    <div className="note-description">
       <h5 className="note-content_title">M t</h5>
       <p className="description-text mt-5">* {renderDescription(product.description)}</p>
       </div>
    <div className="note-content row px-3">
        {note_data && note_data?.map((item: any,index: number) => (
        <div className="col-12 col-sm-12 col-md-6 col-lg-6 col-xl-6 d-flex py-3" style={{alignItems: 'center'}}>
           <div className="note-content_icon">{item.icon}</div>
           <div className="note-content_text">
               <p className="note-content_text-title mb-0">{item.title}</p>
               <p className="note-content_text-content mb-0">{item.text}</p>
           </div>
        </div>
        ))}
        </div>
    </div>
    </>
  );
};

export default ProductDetail;
